import $http from '@/js_sdk/app-request/requestConfig';
// 
export const login = (data) => {
	return $http.post("/login", data).then((res) => {
		return res;
	}).catch(function(error) {
		//
		return error;
	});
};

// 
export const register = (data) => {
	return $http.post("/register", data).then((res) => {
		return res;
	}).catch(function(error) {
		//
		return error;
	});
};
