import $http from '@/js_sdk/app-request/requestConfig';
// 
export const purchaseStorageList = (data) => {
	return $http.get("/purchase_storage/list", data).then((res) => {
		return res;
	}).catch(function(error) {
		//
		return error;
	});
};
// 
